 * {
    box-sizing: border-box;
  }
  
  /* Style inputs */
  input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
  }
  
  input[type=submit] {
    background-color: #04AA6D;
    color: white;
    padding: 12px 20px;
    border: none;
    cursor: pointer;
  }
  
  input[type=submit]:hover {
    background-color: #45a049;
  }
  
  .ass {
    margin: 0 auto;
    width: 700px;
    background: #fff;
    border-radius: 15px;
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.1);
 }
 
 .ass .title {
     margin: 0 auto;
     font-size: 35px;
     font-weight: 600;
     text-align: center;
     line-height: 100px;
     color: #fff;
     user-select: none;
     border-radius: 15px 15px 0 0;
     background: linear-gradient(-135deg, #c850c0, #4158d0);
 }
 
 .ass form {
     padding: 10px 30px 50px 30px;
     
 }
 
 .ass form .field {
     height: 50px;
     width: 100%;
     margin-top: 20px;
     position: relative;
 }
 
 .assform .field input {
     height: 100%;
     width: 100%;
     outline: none;
     font-size: 17px;
     padding-left: 20px;
     border: 1px solid lightgrey;
     border-radius: 25px;
     transition: all 0.3s ease;
 }
 
 .ass form .field input:focus,
 form .field input:valid {
     border-color: #4158d0;
 }
 
 .ass form .field label {
     position: absolute;
     top: 50%;
     left: 20px;
     color: #999999;
     font-weight: 400;
     font-size: 17px;
     pointer-events: none;
     transform: translateY(-50%);
     transition: all 0.3s ease;
 }
 
 form .field input:focus~label,
 form .field input:valid~label {
     top: 0%;
     font-size: 16px;
     color: #4158d0;
     background: #fff;
     transform: translateY(-50%);
 }
 
 form .content {
     display: flex;
     width: 100%;
     height: 50px;
     font-size: 16px;
     align-items: center;
     justify-content: space-around;
 }
 
 form .content .checkbox {
     display: flex;
     align-items: center;
     justify-content: center;
 }
 
 form .content input {
     width: 15px;
     height: 15px;
     background: red;
 }
 
 form .content label {
     color: #262626;
     user-select: none;
     padding-left: 5px;
 }
 
 form .content .pass-link {
     color: "";
 }
 
 form .field input[type="submit"] {
     color: #fff;
     border: none;
     padding-left: 0;
     margin-top: -10px;
     font-size: 20px;
     font-weight: 500;
     cursor: pointer;
     background: linear-gradient(-135deg, #c850c0, #4158d0);
     transition: all 0.3s ease;
 }
 
 form .field input[type="submit"]:active {
     transform: scale(0.95);
 }
 
 form .signup-link {
     color: #262626;
     margin-top: 20px;
     text-align: center;
 }
 
 form .pass-link a,
 form .signup-link a {
     color: #4158d0;
     text-decoration: none;
 }
 
 form .pass-link a:hover,
 form .signup-link a:hover {
     text-decoration: underline;
 }
